import React from 'react';
import Particles from 'react-particles-js';

import './ParticlesWrapper.scss';

function ParticlesWrapper() {
  let particleColor = getComputedStyle(document.documentElement).getPropertyValue('--particleColor');
  return (
    <Particles
        className="Particles"
        params={{
          "particles": {
              "color": {
                "value": [particleColor]
              },
              "number": {
                  "value": 20,
                  "density": {
                      "enable": false,
                      "value_area": 1100
                  }
              },
              "line_linked": {
                  "enable": false,
                  "opacity": 0.1
              },
              "move": {
                  "random": true,
                  "speed": 1
              },
              "size": {
                  "value": 1.8
              },
              "opacity": {
                  "anim": {
                      "enable": true,
                      "speed": 2,
                      "opacity_min": 0.1
                  }
              }
          },
          "interactivity": {
              "events": {
                  "onclick": {
                      "enable": true,
                      "mode": "push"
                  },
                  "onhover": {
                      "enable": true,
                      "mode": "grab"
                  }
              },
              "modes": {
                  "push": {
                      "particles_nb": 1
                  },
                  "grab": {
                      "line_linked": {
                          "opacity": 0.2,
                          "color": "ffffff"
                      }
                  }
              }
          },
          "retina_detect": true
      }} />
  );
}

export default ParticlesWrapper;
