const primaryThemes = [
  ['rgb(102,255,102)', 'rgb(102,255,102,0.07)', '0deg', '#FFFF00'],
  ['rgb(20,200,255)', 'rgb(20,200,255,0.07)', '90deg', '#99FFFF'],
  ['rgb(240,120,235)', 'rgb(240,120,235,0.07)', '180deg', '#FFDDFF'],
  ['rgb(255,104,31)', 'rgb(255,104,31,0.07)', '270deg', '#FFFFFF']
]
  
export function randomTheme(index) {
  if(index === undefined) {
    index = Math.floor(Math.random()*primaryThemes.length);
  }
  let themeVars = primaryThemes[index];
  document.documentElement.style.setProperty('--primaryColor', themeVars[0]);
  document.documentElement.style.setProperty('--primaryBackground', themeVars[1]);
  document.documentElement.style.setProperty('--backgroundHue', themeVars[2]);
  document.documentElement.style.setProperty('--particleColor', themeVars[3]);
}
